import { useTranslation } from "react-i18next";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useState } from "react";

import {
  useDepartmentSyllabusRemove,
  useDepartmentSyllabusUpdate,
} from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import AddDepartmentSiteSyllabus from "./AddDepartmentSiteSyllabus";
const DepartmentSiteSyllabusCardMenu = ({
  dsid,
  onClose,
  menuData,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");

  const [departmentSyllabusRemove] = useDepartmentSyllabusRemove();
  const [departmentSyllabusUpdate] = useDepartmentSyllabusUpdate();
  const onStepClose = () => {
    setStep("");
  };

  const onDelete = () => {
    if (menuData?._id && dsid) {
      setDisabled((pre) => !pre);
      departmentSyllabusRemove({
        dsid: dsid,
        bodyParameter: {
          cid: menuData?._id,
          flow: "SYLLABUS",
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onEditAction = (data) => {
    if (menuData?._id && dsid) {
      onStepClose();
      setDisabled((pre) => !pre);
      departmentSyllabusUpdate({
        dsid: dsid,
        bodyParameter: {
          ...data,
          cid: menuData?._id,
          flow: "SYLLABUS",
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      {step === 1 ? (
        <AddDepartmentSiteSyllabus
          isEdit
          dsid={dsid}
          onEditAction={onEditAction}
          menuData={menuData}
          onClose={onStepClose}
        />
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <h6 onClick={() => setStep(1)}>{t("edit")}</h6>
          <h6 onClick={onDelete}>{t("delete")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default DepartmentSiteSyllabusCardMenu;
