import { useTranslation } from "react-i18next";
import style from "../../../StoreManager/StoreManager.module.css";
import FormFileView from "../../../../Ui/Input/FormFileView";
import { useState } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import DepartmentSiteSyllabusCardMenu from "./DepartmentSiteSyllabusCardMenu";
const DepartmentSiteSyllabusCard = ({ pso, onRefetch, dsid }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);
  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div className={style.smc_card}>
        <div className={style.smc_card_contatiner}>
          <div className={style.smc_card_contatiner}>
            <div className={style.smc_card_contatiner_inner}>
              <h6>
                {" "}
                {t("name")} : {pso?.name}
              </h6>
              <FormFileView
                label={`${t("attachment")} :`}
                value={{
                  key: pso?.attach,
                }}
                customFormInputContainer={{
                  marginBlock: "0",
                }}
              />
            </div>
          </div>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onToggle}
        />
      </div>
      {open && (
        <DepartmentSiteSyllabusCardMenu
          onClose={onToggle}
          dsid={dsid}
          menuData={pso}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default DepartmentSiteSyllabusCard;
